}

.wd-dimension-square {
    width: 75px;
    height: 75px;
}

.wd-pos-relative-nudge-up-right {
    position: relative;
    bottom: 30px;
    left: 30px;
}

.wd-pos-relative-nudge-down-right {
    position: relative;
    top: 20px;
    left: 20px;
}

.wd-pos-relative {
    position: relative;
}

.wd-pos-absolute-10-10 {
    position: absolute;
    top: 10px;
    left: 10px;
}

.wd-pos-absolute-50-50 {
    position: absolute;
    top: 50px;
    left: 50px;
}

.wd-pos-absolute-120-20 {
    position: absolute;
    top: 20px;
    left: 120px;
}

.wd-pos-fixed {
    position: fixed;
    right: 0px;
    bottom: 50%;
}

.wd-zindex-bring-to-front {
    z-index: 10;
}

.wd-float-left {
    float: left;
    height: 100px;
}

.wd-float-right {
    float: right;
    height: 100px;
}

.wd-float-done {
    clear: both;
}

.wd-grid-row {
